.about {
  background-color: var(--bg);
  padding: var(--standart-section-top-padding) var(--standart-side-padding);
}

.about__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-template-areas: "slogan slogan" "title info" "subslogan info";
  width: 100%;
  height: 100%;
}
@media (max-width: 900px) {
  .about__wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
  }
}

.about__title {
  grid-area: title;
  padding-bottom: 10px;
  text-transform: uppercase;
  color: var(--main-text-color);
  letter-spacing: 3px;
}

.about__slogan {
  grid-area: slogan;
  color: var(--main-text-color);
}

.about__header-block {
  padding-bottom: 50px;
}
@media (max-width: 900px) {
  .about__header-block {
    padding-bottom: 0;
  }
}

.about__subslogan {
  grid-area: subslogan;
  color: var(--main-text-color);
}
@media (max-width: 900px) {
  .about__subslogan {
    display: none;
  }
}

.about__info {
  grid-area: info;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 15px;
  color: var(--main-text-color);
}

