.layout {
  display: grid;
  grid-template-areas: "title data data" "info data data" "images images images";
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: min-content min-content 1fr;
  padding: var(--standart-section-top-padding) var(--standart-side-padding);
  width: 100%;
  height: 100%;
  background-color: var(--bg);
  gap: 20px;
}
@media (max-width: 900px) {
  .layout {
    grid-template-areas: "title" "data" "info" "images";
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
  }
}

.layout__title {
  align-self: start;
  grid-area: title;
  text-align: start;
}

.layout__info {
  grid-area: info;
}

.layout__data {
  display: grid;
  grid-area: data;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 30px;
}

.layout__data-item {
  position: relative;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr max-content 1fr;
  padding-left: 40px;
  gap: 40px;
}
.layout__data-item::before {
  content: "";
  position: absolute;
  top: 50%;
  height: 2px;
  background-color: var(--main-color);
  transform: translateY(-50%);
}
@media (max-width: 600px) {
  .layout__data-item::before {
    display: none;
  }
}
.layout__data-item:first-child::before {
  right: 0;
  grid-column: 3/-1;
  width: 100%;
}
.layout__data-item:first-child > .layout__data-title {
  grid-column: 2/3;
}
.layout__data-item:last-child::before {
  right: 0;
  grid-column: 2/-1;
  width: 100%;
}
.layout__data-item:last-child > .layout__data-title {
  grid-column: 1/2;
}
.layout__data-item:nth-child(2)::before {
  right: 0;
  grid-column: 1/2;
  width: 100%;
}
.layout__data-item:nth-child(2) > .layout__data-title {
  grid-column: 3/3;
}
@media (max-width: 600px) {
  .layout__data-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-left: 0;
    width: 100%;
  }
}

.layout__data-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: var(--basci-font);
  font-weight: 500;
  font-size: 30px;
  color: var(--main-text-color);
}
.layout__data-title > span {
  display: flex;
  font-family: var(--basci-font);
  font-weight: 500;
  font-size: 70px;
  white-space: nowrap;
  color: var(--main-color);
}
@media (max-width: 600px) {
  .layout__data-title {
    font-size: 20px;
  }
  .layout__data-title > span {
    font-size: 50px;
  }
}

.layout__images {
  display: grid;
  grid-area: images;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 900px) {
  .layout__images {
    grid-template-columns: 1fr;
  }
}

.layout__image {
  width: 100%;
  object-fit: cover;
}

