  .your-house {
    grid-template-columns: 1fr;
  }
  .your-house > .your-house__link {
    justify-self: flex-start;
  }
}

.your-house__link {
  position: relative;
  align-self: center;
  justify-self: center;
  padding: 5px 0;
  font-family: var(--basci-font);
  font-weight: 400;
  font-size: 20px;
  text-decoration: none;
  color: var(--main-text-color);
  transition: all ease 0.5s;
}
.your-house__link::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 71%;
  height: 2px;
  background-color: var(--main-text-color);
  transition: all ease 0.5s;
}
.your-house__link::after {
  content: "";
  position: absolute;
  left: -20%;
  bottom: 0;
  width: 71%;
  height: 2px;
  background-color: var(--main-text-color);
  transition: all ease 0.5s;
}
.your-house__link:hover {
  color: var(--main-color);
}
.your-house__link:hover::before {
  width: 0%;
}
.your-house__link:hover::after {
  left: 50%;
  width: 0%;
}

